import { Component, OnInit, OnDestroy } from '@angular/core';
import { Location } from '@angular/common';
import { Router } from '@angular/router';
import { Store } from '@ngxs/store';
import { UtilsService } from '../@core/utils.service';
import { BasePage } from '../base/base.page';
import { NavigationExtrasService } from '../@core/navigation-extras.service';

@Component({
  selector: 'app-select-program-location',
  templateUrl: './select-program-location.page.html',
  styleUrls: ['./select-program-location.page.scss'],
})
export class SelectProgramLocationPage extends BasePage implements OnInit, OnDestroy {
  constructor(
    location: Location,
    router: Router,
    store: Store,
    utilsService: UtilsService,
    private navigationExtrasService: NavigationExtrasService
  ) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {}

  public saveLocation($event: any): void {
    this.navigationExtrasService.setExtras({ location: $event });
    this.goBack();
  }

  ngOnDestroy() {}
}
